  color: #2b9aff;
}
.logo-icon {
  display: table-cell;
  vertical-align: middle;
  border-radius: 50%;
  width: 64px;
}
.logo-icon img {
  display: block;
  width: 48px;
  height: 48px;
  background: #FFF;
  border-radius: inherit;
}
.logo-txt {
  display: table-cell;
  vertical-align: middle;
  line-height: 1.2;
}
.logo-name {
  position: relative;
  display: block;
  font-size: 20px;
  font-weight: bold;
}
